.seventh-block {
    margin: 140px auto auto;
    width: 100%;

    &__title {
        font-family: $ff;
        font-size: 48px;
        line-height: 130%;
        text-align: center;
        letter-spacing: $lc;
        color: $rgba-c;
    }

    &__lines {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5px auto;
        width: 412px;

        &>hr:first-child {
            width: 100%;
            height: 0;
            border: 1px solid $main-color;
        }

        &>hr {
            margin-top: 10px;
            width: 220px;
            border: 1px solid $main-color;
        }
    }

    &__text {
        margin-top: 30px;
        font-size: 16px;
        line-height: 150%;
        text-align: center;
        letter-spacing: $lc;
        text-indent: 20px;
        color: $rgba-c;
    }

    &__restourant {
        margin-top: 74px;
        display: flex;
        width: 100%;
        height: 455px;
        background: rgba(191, 219, 247, 0.2);

        &-text {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-left: 40px;
            width: 513px;
            text-align: center;

            &>h2 {
                width: 100%;
                font-family: $ff;
                font-size: 38px;
                line-height: 150%;
                text-align: center;
                letter-spacing: $lc;
                color: $rgba-c;
            }

            &>button {
                margin-top: 16px;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 184.62px;
                height: 36.29px;
                background: $main-color;
                border: none;
                cursor: pointer;

                &>span {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 86.1px;
                    color: #FFFFFF;
                    font-size: 20px;
                    font-family: $ff;
                }
            }

            &>p {
                margin-top: 20px;
                font-size: 20px;
                line-height: 130%;
                text-align: center;
                letter-spacing: $lc;
                color: $rgba-c;
            }

            &>p:last-child {
                margin-top: 20px;
                font-size: 16px;
                line-height: 130%;
                text-align: center;
                letter-spacing: $lc;
                text-indent: 20px;
                color: $rgba-c;
            }
        }

        &-img {
            position: relative;
            width: 540px;
            height: 100%;

            &>div {
                width: 100%;
            }

            &-item {
                position: absolute;
                inset: 0;
                opacity: 0;
                transition: 0.9s;

                &>img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }

            &-item[data-active] {
                opacity: 1;
                z-index: 1;
                transition-delay: 0ms;
            }

            &-btn {
                position: absolute;
                z-index: 2;
                background: none;
                border: none;
                top: 50%;
                transform: translateY(-50%);
                cursor: pointer;

                &.prev {
                    left: 0;
                }
                &.next {
                    right: 0;
                }
            }
        }
    }

    &__terrace {
        margin-top: 74px;
        display: flex;
        justify-content: space-between;
        width: 100%;

        &-text {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 513px;
            text-align: center;

            &>h2 {
                width: 100%;
                font-family: $ff;
                font-size: 38px;
                line-height: 150%;
                text-align: center;
                letter-spacing: $lc;
                color: $rgba-c;
            }

            &>button {
                margin-top: 16px;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 184.62px;
                height: 36.29px;
                background: $main-color;
                border: none;
                cursor: pointer;

                &>span {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 86.1px;
                    color: #FFFFFF;
                    font-size: 20px;
                    font-family: $ff;
                }
            }

            &>p {
                margin-top: 20px;
                font-size: 20px;
                line-height: 130%;
                text-align: center;
                letter-spacing: $lc;
                color: $rgba-c;
            }

            &>p:last-child {
                margin-top: 20px;
                font-size: 16px;
                line-height: 130%;
                text-align: center;
                letter-spacing: $lc;
                text-indent: 20px;
                color: $rgba-c;
            }
        }

        &-img {
            position: relative;
            width: 540px;
            height: 100%;

            &>div {
                width: 100%;
            }

            &-item {
                position: absolute;
                inset: 0;
                opacity: 0;
                transition: 0.9s;

                &>img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }

            &-item[data-active] {
                opacity: 1;
                z-index: 1;
            }

            &-btn {
                position: absolute;
                z-index: 2;
                background: none;
                border: none;
                margin-top: 40%;
                transform: translateY(-50%);
                cursor: pointer;

                &.prev {
                    left: 0;
                }
                &.next {
                    right: 0;
                }
            }
        }
    }
}