.tab-block {
    margin: 140px auto auto;

    &__title {
        font-family: $ff;
        font-size: 48px;
        line-height: 130%;
        text-align: center;
        letter-spacing: $lc;
        color: $rgba-c;
    }

    &__lines {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5px auto;
        width: 412px;

        &>hr:first-child {
            width: 100%;
            height: 0;
            border: 1px solid $main-color;
        }

        &>hr {
            margin-top: 10px;
            width: 220px;
            border: 1px solid $main-color;
        }
    }

    &-slide {
        position: relative;
        display: none;
        width: 1260px;
        margin-top: 40px;
        margin-right: 0;
        background: rgba(191, 219, 247, 0.2);

        &__img {
            width: 100%;
            height: 538px;
        }

        &__items {
            position: absolute;
            display: flex;
            width: 100%;
            justify-content: space-between;
            inset: 0;
            opacity: 0;
            transition: 0.7s;

            &>img {
                display: block;
                width: 538px;
                object-fit: cover;
                object-position: center;
            }

            &-text {
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 157px;
                justify-content: space-between;
                margin: auto;

                &>p {
                    font-family: $ff;
                    font-weight: 400;
                    font-size: 20px;
                    line-height: 150%;
                    text-align: center;
                    letter-spacing: $lc;
                    color: $rgba-c;
                }
            }
        }

        &__items[data-active] {
            opacity: 1;
            z-index: 1;
        }

        &__btn {
            position: absolute;
            z-index: 2;
            background: none;
            border: none;
            top: 55%;
            transform: translateY(-50%);
            cursor: pointer;

            &.prev {
                left: 0;
            }
            &.next {
                right: 6em;
            }
        }
    }

    &-slides--active {
        display: flex;
    }

    &__tabs {
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 1110px;

        &-items {
            display: flex;
            width: 100%;
            justify-content: space-between;

            &__item {
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                font-weight: 200;
                font-size: 20px;
                line-height: 36px;
                letter-spacing: $lc;
                color: rgba(0, 0, 0, 0.5);
                border-bottom: 1px solid $main-color;
                width: 100%;
                height: 52px;
                transition: 0.7s;
            }

            &__item--active {
                border: 1px solid $main-color;
                border-bottom: none;
                font-weight: 200;
                font-size: 20px;
                line-height: 36px;
                letter-spacing: $lc;
                color: #000000;
            }
        }

        &-body {
            &__block {
                display: none;
                margin-top: 80px;
                right: 0;
                width: 1260px;
                height: 538px;
                background: rgba(191, 219, 247, 0.2);
            }

            &__block--active {
                display: flex;
                transition: 0.7s;
            }

            
            // &__block:target {
            //     display: flex;
            // }

            &__text {
                margin: auto;

                &>p {
                    font-family: $ff;
                    margin-left: 30px;
                    font-size: 20px;
                    line-height: 150%;
                    text-align: center;
                    letter-spacing: $lc;
                    text-indent: 20px;
                    color: $rgba-c;
                    width: 539px;
                }

                &>a {
                    display: block;
                    margin-top: 10px;
                    font-family: $ff;
                    font-size: 20px;
                    line-height: 150%;
                    text-align: center;
                    letter-spacing: $lc;
                    text-indent: 20px;
                    color: $rgba-c;
                }
            }

        }

    }
}